import React from 'react';
import styles from '../css/About.module.css'; // Import CSS module
import shoshiAndJacob from '../images/shoshiandjacob.jpeg';

function About({ language }) {
    return (
        <div className={`${styles['about-container']} ${language === 'he' ? styles['about-rtl'] : styles['about-ltr']}`}>
            <video autoPlay loop muted className={styles['about-background-video']}>
                <source src="" type="video/mp4" />
                Your browser does not support the video tag.
            </video>
            <div className={styles['about-content']}>
                <div className={styles['about-image-container']}>
                    <img src={shoshiAndJacob} alt="Art Khayat" className={styles['about-image']} />
                    <div className={styles['about-image-text']}>
                        <p>{language === 'he' ? '      .' : 'We are happy to assist you and answer any questions.'}</p>
                        <p>{language === 'he' ? <>,<br /> <br /> </> : <>Best regards,<br />Michal Bukris<br />Riki Khayat</>}</p>
                    </div>
                </div>
                <div className={styles['about-text-container']}>
                    <h1>{language === 'he' ? '  -   ' : 'Art Khayat - Painting a Love Story'}</h1>
                    <p>{language === 'he' ? '      ,     ,      ".' : 'Art Khayat was born out of creativity and a love for art, intertwined with the love story of our parents, the artists Shoshi and Yaakov Khayat.'}</p>
                    <p>{language === 'he' ? ' :     ,    " "    .' : 'Nice to meet you: Michal Bukris and Riki Khayat, their daughters, owners of "Art Khayat," and managers of the estate they left behind.'}</p>
                    <p>{language === 'he' ? '        ".   ,         50  :    , , , ,   ,          .' : 'Many of their works are displayed in galleries and among art collectors in Israel and abroad. The collection available for sale includes a wide range of artworks from different periods over 50 years of creation: original oil and acrylic paintings, watercolors, drawings, prints, artistic wall carpets, sculptures, and poetry and novel books based on Shoshi\'s personal diaries.'}</p>
                    <p>{language === 'he' ? '   .   ,   .  .     ,       ,            .' : 'The two were inseparable from the moment they met until death. Their love and connection not only resulted in two daughters, Riki and Michal, and five grandchildren, but also in each of their long careers as unique artists in their own right.'}</p>
                    <p>{language === 'he' ? '     ,      -  " .      -     -.' : 'In addition to being a painter and artist, Yaakov Khayat founded the Association of Painters and Sculptors in Bat Yam and was its first chairman. He also managed the Ben-Ari Museum and promoted Israeli art in Bat Yam.'}</p>
                    <p>{language === 'he' ? '   " "   , ,  .           " ".' : 'Shoshi Khayat, known as "The Clown Painter," was a versatile artist, sculptor, poet, and writer. She won a literary award from the Culture Fund for her poetry book "Red Strawberries.'}</p>
                    <p>{language === 'he' ? '  ,  ,        .         ,     " "     ,        ,            11,     ,               .' : 'Their memories and desires, their pain and sadness, joy and optimism, were all captured on canvas. To understand each of their sources of inspiration, you are invited to visit the "Art Khayat" website and delve into their fascinating life story, and their unique love that ignited from a chance meeting in Sakhne, connecting Yaakov, born in Iraq and who came to Israel with his family when he was 11, with Shoshi, from the Gantz family, born to Holocaust survivor parents in Romania after the war and who came to Israel with her mother when she was two.'}</p>
                </div>
            </div>
        </div>
    );
}

export default About;
