import React from 'react';
import styles from '../css/Footer.module.css'; // Import the CSS module

function Footer({ language }) {
    return (
        <footer className={styles.footer} dir={language === 'he' ? 'rtl' : 'ltr'}>
            <div className={styles['footer-content']}>
                <h2>{language === 'he' ? '  | ArtChayat' : 'Art Chayat'}</h2>
                <p> {new Date().getFullYear()} {language === 'he' ? '  .' : 'All rights reserved.'}</p>
                <div className={styles['footer-links']}>
                    <a href="/">{language === 'he' ? '' : 'Home'}</a>
                    <a href="/gallery">{language === 'he' ? '' : 'Gallery'}</a>
                    <a href="/about">{language === 'he' ? '' : 'About'}</a>
                    <a href="/contact">{language === 'he' ? ' ' : 'Contact'}</a>
                    <a href="/shop">{language === 'he' ? '' : 'Shop'}</a>
                </div>
                <p>{language === 'he' ? '   :' : 'Follow us on social media:'}</p>
                <div className={styles['social-media']}>
                    <a href="https://www.facebook.com" target="_blank" rel="noopener noreferrer">
                        {language === 'he' ? '' : 'Facebook'}
                    </a>
                    <a href="https://www.instagram.com" target="_blank" rel="noopener noreferrer">
                        {language === 'he' ? '' : 'Instagram'}
                    </a>
                    <a href="https://www.twitter.com" target="_blank" rel="noopener noreferrer">
                        {language === 'he' ? '' : 'Twitter'}
                    </a>
                </div>
            </div>
        </footer>
    );
}

export default Footer;
